import React, { Component } from 'react';
import './App.scss';

async function loadData() {
  const url = "https://www.instagram.com/explore/tags/%EC%A0%9C%EC%A3%BC%EB%A7%9B%EC%A7%91/?__a=1";
  const res = await fetch(url);
  const resultString = await res.text();

  return JSON.parse(resultString);
}

class App extends Component {
	constructor(props) {
		super(props);
    this.state = {
      page: 0,
      datas: [],
    };
  }

  componentDidMount = () => {
    this.loadNextPage();
  }

  async loadNextPage () {
    let getDatas = await loadData();
    getDatas = getDatas.graphql.hashtag.edge_hashtag_to_media.edges;

    this.setState({datas: getDatas});
  }

  render() {
    return (
      <div className="App_top">
        {this.state.datas.map(item => <InstagramItem
          thumbnail = {item.node.thumbnail_src}
          comment = {item.node.edge_media_to_caption.edges[0].node.text}
          key={item.node.id} />)}
      </div>
    )
  }
}

const InstagramItem = props => (
  <div className="insta_post">
    <div className="insta_img"><img src={props.thumbnail} /></div>
    <div className="insta_comment">{props.comment}</div>
  </div>
);

export default App;



